import React from "react";
import homeFurnitures from "../../assets/img/homeFurniture.jpg"
import commercialFurnitures from "../../assets/img/commercialFurnitures.jpg"
import decorationAndFinishing from "../../assets/img/decorationAndFinishing.jpg"

const ExploreCards = () => {
    return (
        <div className="container-lg my-3">
            <div className="row">
                <div className="col-md-4 my-3 px-3">
                    <div className="position-relative w-100 h-100">
                        <div className="explore-card-hover">
                            <p className="d-none">Home Furniture</p>
                        </div>
                        <img className="w-100 h-100" src={homeFurnitures} alt=""/>
                    </div>
                </div>

                <div className="col-md-4 my-3 px-3 position-relative">
                    <div className="position-relative w-100 h-100">
                        <div className="explore-card-hover">
                            <p className="d-none">Commercial Furniture</p>
                        </div>
                        <img className="w-100 h-100" src={commercialFurnitures} alt=""/>
                    </div>
                </div>

                <div className="col-md-4 my-3 px-3 position-relative">
                    <div className="position-relative w-100 h-100">
                        <div className="explore-card-hover">
                            <p className="d-none">Decoration and Finishing</p>
                        </div>
                        <img className="w-100 h-100" src={decorationAndFinishing} alt=""/>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default ExploreCards;