import React from "react";
import logo from "../../assets/img/mainLogo.jpg"

const SearchSection = props => {
    return(
        <div className="container-fluid bg-dark-custom">
            <div className="container-lg py-4">
                <div className="row">
                    <div className="col-lg-3 text-center overflow-hidden">
                        <img className="logo-img" src={logo} alt=""/>
                    </div>
                    <div className="col-lg-5 overflow-hidden px-0">
                        <div className="text-center pt-2">
                            <select name="" id="">
                                <option value="">All Category</option>
                            </select>
                            <input type="search"/>
                            <button>Se</button>
                        </div>
                    </div>
                    <div className="col-lg-4 text-right overflow-hidden">
                        <ul className="navbar-nav d-block text-nowrap">
                            <li className="nav-item nav-inline mx-2"><a className="text-white" href="/">Your Wishlist</a></li>
                            <li className="nav-item nav-inline mx-2"><a className="text-white" href="/">Your Cart</a></li>
                            <li className="nav-item nav-inline mx-2">
                                <button className="text-white closebtn" onClick={() => props.openNav()}>Menu</button>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default SearchSection;